.myModal {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgba(0,0,0, 0.4);
    font-size: 16px;
    font-family: sans-serif;
    font-weight: 700;
    color: #444;
}

.myModalContent {
    padding: 25px;
    background: white;
    border-radius: 16px;
    min-width: 400px;
    background: #323030;
    font-size: 16px;
    font-family: sans-serif;
    font-weight: 700;
    color: #444;
}
.myModal.active {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    font-family: sans-serif;
    font-weight: 700;
    color: #444;
}